.container {
  background-color: #fff;
  width: 100%;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.container:before {
  content: "";
  z-index: 6;
  background: linear-gradient(-45deg, #ff4167, #e9338e, #ed66a3);
  border-radius: 50%;
  width: 2000px;
  height: 2000px;
  transition: all 1.8s ease-in-out;
  position: absolute;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
}

.form-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.signin-signup {
  z-index: 5;
  grid-template-columns: 1fr;
  width: 50%;
  transition: all 1s ease-in-out .7s;
  display: grid;
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
}

form {
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: center;
  align-items: center;
  padding: 0 5rem;
  transition: all .2s ease-in-out .7s;
  display: flex;
  overflow: hidden;
}

form.sign-in-form {
  z-index: 2;
}

form.sign-up-form {
  z-index: 1;
  opacity: 0;
}

.title {
  color: #444;
  margin-bottom: 10px;
  font-size: 2.2rem;
}

.input-field {
  background-color: #f0f0f0f0;
  border-radius: 55px;
  grid-template-columns: 15% 85%;
  width: 100%;
  max-width: 380px;
  height: 55px;
  margin: 10px 0;
  padding: 0 .4rem;
  display: grid;
}

.input-field i {
  text-align: center;
  color: #acacac;
  font-size: 1.1rem;
  line-height: 55px;
}

.input-field input {
  color: #333;
  background: none;
  border: none;
  outline: none;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
}

.input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}

.btn {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background: linear-gradient(to top, #ff4167, #e9338e, #fd328d);
  border: none;
  border-radius: 49px;
  outline: none;
  width: 150px;
  height: 50px;
  margin: 20px 0;
  font-weight: 600;
  transition: all .5s;
}

.btn:hover {
