}

.panel-container {
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
}

.panel {
  text-align: center;
  z-index: 7;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  display: flex;
}

.panel .content {
  color: #fff;
  transition: all 1.1s ease-in-out .4s;
}

.panel h3 {
  margin: -20px 0 0;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
}

.panel p {
  margin-bottom: 10px;
  padding: .7rem 0;
  font-size: 1rem;
}

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.right-panel .content, .right-panel .image {
  transform: translateX(800px);
}

.btn.transparent {
  background: none;
  border: 2px solid #f0f0f0f0;
  width: 150px;
  height: 45px;
  margin: 10px 0 0;
  font-size: 1rem;
  font-weight: 600;
}

.image {
  width: 100%;
  transition: all 1.1s ease-in-out .4s;
}

.container.sign-up-mode:before {
  right: 52%;
  transform: translate(100%, -50%);
}

.container.sign-up-mode .left-panel {
  pointer-events: none;
}

.container.sign-up-mode .left-panel .image, .container.sign-up-mode .left-panel .content {
  transform: translateX(-800px);
}

.container.sign-up-mode .right-panel {
  pointer-events: all;
}

.container.sign-up-mode .right-panel .content, .container.sign-up-mode .right-panel .image {
  transform: translateX(0);
}

.container.sign-up-mode .signin-signup {
  left: 25%;
}

.container.sign-up-mode form.sign-in-form {
  z-index: 1;
  opacity: 0;
}

.container.sign-up-mode form.sign-up-form {
  z-index: 2;
  opacity: 1;
}

@media (width <= 870px) {
  .container {
    height: 100vh;
    min-height: 800px;
  }

  .container:before {
    bottom: 68%;
    left: 30%;
    right: initial;
    top: initial;
    width: 1500px;
    height: 1500px;
    transition: all 2s ease-in-out;
    transform: translateX(-50%);
  }

  .signin-signup {
    width: 100%;
    transition: all 1s ease-in-out .8s;
    top: 95%;
    left: 50%;
    transform: translate(-50%, -100%);
  }

  .panel-container {
    grid-template-rows: 1fr 2fr 1fr;
    grid-template-columns: 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
  }

  .panel .content {
    padding-right: 15%;
    transition: all .9s ease-in-out .8s;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    padding: .5rem 0;
  }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: .7rem;
  }

  .image {
    width: 200px;
    transition: all .9s ease-in-out .6s;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .right-panel {
    grid-row: 3 / 4;
  }

  .right-panel .content, .right-panel .image {
    transform: translateY(300px);
  }

  .container.sign-up-mode:before {
    bottom: 32%;
    right: initial;
    transform: translate(-50%, 100%);
  }

  .container.sign-up-mode .left-panel .image, .container.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .container.sign-up-mode .signin-signup {
    top: 5%;
    left: 3%;
    transform: translate(0);
  }
}

@media (width <= 570px) {
  form {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }

  .panel .content {
    padding: .5rem 1rem;
  }

  .container:before {
    bottom: 72%;
    left: 50%;
  }

  .container.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}
